#main-navigation-bar{
  display: flex;
  justify-content: center;
  padding: 40px 0px;
}

#main-navigation-bar a.active .navigation-item{
  background: #5867DD;
  color: white;
}

#main-navigation-bar a.active svg{
  stroke: white;
}

.navigation-item{
  height: 60px;
  padding: 10px 15px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: white;
  -webkit-box-shadow:2px 2px 8px  rgb(165, 185, 221);
  -moz-box-shadow: 2px 2px 8px #DCE0E7;
  -o-box-shadow: 2px 2px 8px #DCE0E7;
  box-shadow: 2px 2px 8px #DCE0E7;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -o-border-radius: 5px;
  border-radius: 5px;
  color: rgb(122, 134, 221);
  margin: 0px 5px;
}

.navigation-item svg{
  stroke: rgb(122, 134, 221);
}

.navigation-item:hover{
  cursor: pointer;
}

.navigation-item:hover svg{
  stroke: #5867DD;
  stroke-width: 2.2;
}

#download-form,
#download-list{
  width: 80%;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 40px auto;
  font-family: Arial, Helvetica, sans-serif;
  text-align: center;
  padding: 20px;
  font-size: 32px;
  -webkit-box-shadow:2px 2px 8px  rgb(161, 178, 209);
  -moz-box-shadow: 2px 2px 8px rgb(161, 178, 209);
  -o-box-shadow: 2px 2px 8px rgb(161, 178, 209);
  box-shadow: 2px 2px 8px rgb(161, 178, 209);
  background: white;
}

.form-holder{
  display: flex;
}

.form-holder input{
  color: rgb(182, 187, 194);
  font-size: 16px;
  padding: 15px;
  border: 1px solid rgb(203, 215, 236);
  background: transparent;
  box-sizing: border-box;
  outline: none;
  transition: all;
}

.form-holder input[type='text']{
  border-radius: 5px 0px 0px 5px;
  color: rgb(70, 70, 70);
  font-weight: 400;
  min-width: 40vw;
}
.form-holder input[type="submit"]{
  border-radius: 0px 5px 5px 0px;
  border-left: none;
}

.form-holder input[type='text']:focus{
  border: 1px solid rgb(127, 166, 233);
}

.form-holder input[type="submit"]:hover{
  background: rgb(127, 166, 233);
  color: white;
  cursor: pointer;
}

.form-holder input[type='text']::placeholder{
  color: rgb(126, 150, 190);
  font-weight: bold;
  font: inherit;
}


.download-item{
  width: 90%;
  padding: 15px;
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  -webkit-box-shadow:2px 2px 12px  rgb(161, 178, 209);
  -moz-box-shadow: 2px 2px 12px rgb(161, 178, 209);
  -o-box-shadow: 2px 2px 12px rgb(161, 178, 209);
  box-shadow: 2px 2px 12px rgb(161, 178, 209);
}

.download-item .thumbnail{
  width: 80px;
  height: 80px;
  align-self: center;
}

.download-item .download-btn{
  font-size: 16px;
  font-weight: bold;
  color: rgb(34, 34, 34);
  display: flex;
  align-items: center;
}

.download-item .video-details{
  display: flex;
  flex-direction: column;
  width: 78%;
  font-size: 16px;
  text-align: left;
}

.video-details p{
  font-size: 1.1em;
  color: #5867DD;
}

.video-details p.range{
  color: grey;
  font-size: .8em;
}

.video-details sub.percentage{
  display: block;
  padding-top: 10px;
}

.video-quality{
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: .8em;
  font-weight: lighter;
  padding: 10px 0px;
}

.video-quality:hover{
  cursor: pointer;
  color: #5867DD;
  background: rgb(228, 228, 228);
}

.download-btn a{
  font-size: .8em;
  color: gray;
  font-weight: light;
}